const slideIn = keyframes`
  0% {
    opacity: 0;
    pointer-events: none;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    pointer-events: normal;
    transform: translateX(0);
  }
`;

const Home = styled(_Home)`

  #banner-img-wrap {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
  }

  #mug {
    display: none;

    @media (min-width: 1000px) {
      display: block;
      height: 250px;
      width: auto;
      position: absolute;
      bottom: 30vh;
      left: 5vw;

      #mug-body path {
        fill: ${props => props.theme.color.mugBody};
      }

      #mug-heart path {
        fill: ${props => props.theme.color.mugHeart};
      }

      #mug-text path {
        fill: ${props => props.theme.color.mugText};
      }
    }

    @media (min-width: 1200px) {
      left: 10vw;
    }
  }

  #rtj {
    display: none;

    @media (min-width: 1000px) {
      display: block;
      height: 150px;
      width: auto;
      position: absolute;
      top: 17vh;
      right: 10vw;

      #rtj-board {
        stroke: ${props => props.theme.color.contrast};
      }

      #rtj-text {
        /* fill: ${props => props.theme.color.contrast}; */
      }

      #rtj-lines line {
        stroke: ${props => props.theme.color.contrast};
      }

      #rtj-corners line {
        stroke: ${props => props.theme.color.primary};
      }
    }

    @media (min-width: 1200px) {
      right: 15vw;
    }
  }

  #laptop {
    height: auto;
    max-width: 100vw;

    &:hover {
      #laptop-text,
      #laptop-flash {
        opacity: 0;
        transition: opacity .1s;
      }

      #laptop-heart {
        opacity: 1;
        transition: opacity .1s .05s;
      }
    }
  }

  #laptop-monitor {
    fill: ${props => props.theme.color.laptopMonitor};
    stroke: rgba(0, 0, 0, .1);
  }

  #laptop-screen {
    fill: ${props => props.theme.color.contrast};
    stroke: rgba(0, 0, 0, .1);
  }

  #laptop-base {
    fill: ${props => props.theme.color.laptopBase};
    stroke: rgba(0, 0, 0, .1);
  }

  #laptop-text {
    fill: ${props => props.theme.color.primary};
  }

  #laptop-text,
  #laptop-flash {
    transition: opacity .1s .05s;
  }

  #laptop-heart {
    fill: ${props => props.theme.color.primary};
    opacity: 0;
    transition: opacity .1s;
  }

  h1 {
    padding: 2rem;
    font-family: ${props => props.theme.font.sans};
    font-weight: bold !important;
    font-size: 1.5rem;
    text-align: left;
    background-color: ${props => props.theme.color.primary};
    color: ${props => props.theme.color.contrast} !important;
    box-shadow: 16px 16px 0 ${props => props.theme.color.contrastTransparent};

    @media (min-width: 768px) {
      max-width: 800px;
      margin-left: auto;
      margin-right: auto;
      padding: 4rem;
      padding-left: 5rem;
      padding-right: 2rem;
      border: none;
      font-size: 2.5rem !important;
    }
  }

  a {
    background-color: ${props => props.theme.color.contrastTransparent};
    color: ${props => props.theme.color.primary} !important;
    animation: ${slideIn} .5s 2s backwards;
  }
`;

export default Home;